import React from 'react';

import styles from 'styles/post.module.scss';
import PlaystoreBanner from 'components/play-store';

class EquityFunds extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L3 : WTF ARE MUTUAL FUNDS</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Equity Funds</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <div className={styles["tldr"]}>
                <h2 className={styles["heading"]}>‚ö°Ô∏è Here‚Äôs what this lesson is all about :</h2>
                <p>In this chapter and the next, we explain how an equity fund works and the different types of Equity Funds. </p>
            </div>
            <p>Compared to Debt Funds, understanding Equity funds are much easier.</p>
            <br/>
            <p className={styles["highlight"]}>Equity Funds take your money and invest it in stocks of companies. </p>
            <br/>
            <p>Now, based on how they make these investments, there are multiple types of Equity Funds.</p>
            <br/>
            <h5>LARGE CAP FUNDS</h5>
            <p>Like the name indicates, Large Cap Funds invest at least 80% of their money in Large Cap stocks <span style={{fontWeight: "600"}}><i>(Top 100 most valuable companies in India. Companies like Reliance, TCS, etc are Large Caps).</i></span></p>
            <br/>
            <p>Here the risk is lower than a Medium-Cap or a Small-Cap fund. You also get returns that are in line with how the overall markets perform.</p>
            <br/>
            <h5>MID CAP FUNDS</h5>
            <p>Medium-Cap funds invest 80% of their money in Medium Cap Stocks <span style={{fontWeight: "600"}}><i>(101st to 250th most valuable companies in India)</i></span>. Medium Caps are riskier than large-cap funds.</p>
            <br/>
            <h5>SMALL CAP FUNDS</h5>
            <p>Small-Cap funds invest 80% of their money in Small Cap Stocks <span style={{fontWeight: "600"}}><i>(Every other company that is publicly listed i.e, you can buy their stock on the stock exchange)</i></span>. Small Caps are the riskiest among all three.</p>
            <br/>
            <h5>MULTI CAP FUNDS</h5>
            <p>In a multi cap fund,</p>
            <p>‚Ä¢ Atleast 25% of the money is to invested in Small-Caps</p>
            <p>‚Ä¢ Atleast 25% of the money is to invested in Medium-Caps</p>
            <p>‚Ä¢ Atleast 25% of the money is to invested in Large-Caps</p>
            <br/>
            <p>This sort of takes your money and spreads it across different risk levels.</p>
            <br/>
            <h5>FLEXI CAP FUNDS</h5>
            <p>In a Flexi-Cap fund, the fund manager is free to invest the money wherever he/she chooses.</p>
            <br/>
            <p>There is no restriction on which type of company the fund manager has to choose.</p>
            <br/>
            <p style={{color: "var(--secondary-highlight)"}}>Here you are betting completely on the Fund Manager to make good choices on your behalf.</p>
            <br/>
            <PlaystoreBanner text={"Try out Savemo Infiniti"}/>
          </div>
      </div>
    );
  }
}

export default EquityFunds;