import React from 'react';

import styles from 'styles/post.module.scss';
import equation from './equation.svg';
import getIt from './get-it.gif';

class MutualFundsComponents extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L3 : WTF ARE MUTUAL FUNDS</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Decoding Mutual Funds</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <div className={styles["tldr"]}>
                <h2 className={styles["heading"]}>‚ö°Ô∏è Here‚Äôs what this lesson is all about :</h2>
                <p>Mutual Funds make money by charging you fees. The returns that these funds give are after they take the fees.</p>
                <br/>
                <p>Let's check out what the fees are üëá</p>
            </div>
            <p>Now that you know how mutual funds work, it‚Äôs time to take a deep dive into what a Mutual Fund consists of.</p>
            <br/>
            <p>Let‚Äôs start with fees.</p>
            <br/>
            <h5>FEES</h5>
            <p>In return for managing your money, the Mutual Fund takes some money to manage the Fund. This is called <span style={{color: "var(--secondary-highlight)"}}>Expense Ratio</span>.</p>
            <br/>
            <p>Another fee that some Mutual Funds charge is <span style={{color: "var(--secondary-highlight)"}}>Exit Load</span>. This is charged when you withdraw the money before a stipulated time period.</p>
            <br/>
            <p>Why do they charge this though ü§î?</p>
            <br/>
            <div className={styles["box"]}>
              <p>Mutual Funds that invest stock, do so based on long term potential potential, so it becomes very difficult to the Mutual Fund to manage money if the investors keep withdrawing money before a certain time-frame. So in such funds, they add an <span style={{color: "var(--secondary-highlight)"}}>Exit Load</span> to discourage you from withdrawing money too soon.</p>
            </div>
            <br/>
            <p>These Fees vary for different funds. A lot of funds don't charge Exit Load and have a very low Expense Ratio, while some funds even have a lock-in period!</p>
            <br/>
            <p>Next, let's see how you invest in a Mutual Fund and how the value of your investment goes up or down.</p>
            <br/>
            <h5>NAV</h5>
            <p>To make it easier for people to invest, Mutual Funds are sold in units. The price of a unit is called <span style={{color: "var(--secondary-highlight)"}}>NAV</span>.</p>
            <br/>
            <p>So when a mutual Fund has an NAV of ‚Çπ298, it means that one unit costs ‚Çπ298.</p>
            <p>When you invest in a Mutual Fund you are given units for the amount you have invested.</p>
            <br/>
            <p>So do you have to invest in multiples on NAV?</p>
            <br/>
            <p>Nope. You can invest <b>whatever</b> amount you want, and units will be allocated towards that.</p>
            <br/>
            <p className={styles["highlight"]}>Say, for example, the NAV of a Mutual Fund is ‚Çπ100 and you want to invest ‚Çπ150. Once you invest the money, you will be issued 1.5 units.</p>
            <br/>
            <p>So now what?</p>
            <br/>
            <div className={styles["box"]}>
              <p>When the value of investments that the mutual fund has made goes up, the NAV goes up. üìà</p>
              <p>When the value of investments that the mutual fund has made goes down, the NAV goes down. üìâ</p>
            </div>
            <br/>
            <p>In the previous example, you bought 1.5 units at an NAV of ‚Çπ100.</p>
            <br/>
            <p>Say one year later, the investments the fund made performed well and the NAV of the fund increased to ‚Çπ200 üòç</p>
            <br/>
            <p>So when you redeem your investment, you'll get 1.5 (number of units you own)*200( current NAV) = ‚Çπ300.</p>
            <p style={{fontWeight: "600"}}><i>ü§ë Cha-Ching !!! ü§ë</i></p>
            <br/>
            <p>So how is the NAV calculated?</p>
            <br/>
            <img src={equation}/>
            <br/><br/>
            <p>Notice that expenses are already subtracted in the NAV. </p>
            <br/>
            <p style={{color: "var(--secondary-highlight)"}}>So the expense ratio we talked about is already accounted for in the NAV. You don't have to pay anything extra.</p>
            <br/>
            <p>This also means that the returns you see are inclusive of the expenses.</p>
            <br/>
            <img src={getIt}/>
            <br/><br/>
            <p>Now you know what goes on in a mutual fund, the different fees you gotta pay and how the Units are calculated.</p>
            <br/>
            <p>But which ones to buy, when there are so many funds out there?</p>
            <p>Glad you asked ‚Äòcos <b>Savemo</b> is here to help. Read on to know which ones to save your money in ü§ô</p>
          </div>
      </div>
    );
  }
}

export default MutualFundsComponents;