import React from 'react';

import styles from 'styles/post.module.scss';
import list from './list.png';
import debt from './debt.svg';
import bag from './bag.svg';

class EvaluatingDebtFunds extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L3 : WTF ARE MUTUAL FUNDS</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Evaluating Debt Funds</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <div className={styles["tldr"]}>
                <h2 className={styles["heading"]}>‚ö°Ô∏è Here‚Äôs what this lesson is all about :</h2>
                <p>Debt Funds have 1. Credit Risk, 2. Interest Rate Risk and 3.Liquidity Risk.</p>
                <p>We'll help you assess them for any debt fund.</p>
            </div>
          <p>Evaluating a Debt Fund is simple, you just have to evaluate the different risks it has. We'll show you how to do that.</p>
          <br/>
          <h5>EVALUATING CREDIT RISK</h5>
          <p>The first risk to measure is the Credit Risk. To measure credit risk, all you have to do is take a look at their debts.</p>
          <br/>
          <p>It looks something like this:</p>
          <img src={list}/>
          <br/><br/>
          <p><span style={{color: "var(--secondary-highlight)"}}><b>AAA</b></span> is the highest rating a company can get.</p>
          <br/>
          <p>Below that is <span style={{color: "var(--secondary-highlight)"}}><b>AA+, AA, AA-, A+</b></span> and so on.</p>
          <br/>
          <p><span style={{color: "var(--secondary-highlight)"}}><b>Sovereign</b></span> means that the debt is given to the Government. Debt given to the Govt. has 0 risk (because it‚Äôs Government üëÄ)</p>
          <br/>
          <p><span style={{color: "var(--secondary-highlight)"}}><b>Cash</b></span> is the cash they hold.</p>
          <br/>
          <p><span style={{color: "var(--secondary-highlight)"}}><b>Unrated</b></span> refers to the debt they have given to companies that don't have a credit rating &#9760;&#65039;</p>
          <br/>
          <p><i>So when you want to evaluate a Fund, check what % of their holdings are in AAA rated debt, sovereign and in cash.</i></p>
          <br/>
          <p style={{color: "var(--secondary-highlight)"}}><i><b>In this case, 74.17% of the debts are to AAA rated companies, 5.33% is with the Government and 3.7% is in cash. So around 83% of the debt is of the highest quality.</b></i></p>
          <br/>
          <p style={{color: "var(--secondary-highlight)"}}><i><b>Higher this number, the less riskier it is.</b></i></p>
          <br/>
          <div className={styles["separator"]}></div>
          <br/>
          <h5>EVALUATING INTEREST RISK</h5>
          <p>The Second risk we have check is the interest rate risk. Fortunately, this is straightforward.</p>
          <br/>
          <p>Every Fund has a number called <span style={{color: "var(--secondary-highlight)"}}><b>Macaulay duration</b></span>. This basically indicates the time required to recover the money we have given as debt. We can use this to evaluate the risk. </p>
          <br/>
          <p>Macaulay duration gives the % change in NAV if the interest rates change by 1%</p>
          <br/>
          <p><i>Eg: If the Macaulay duration for Debt Fund A is 1.5 years,</i></p>
          <p><i>if interest increases by 1%, the NAV of the fund drops by 1.5%,</i></p>
          <p><i>if the interest rate decreases by 2%, the NAV of the fund increases by 3%</i></p>
          <br/>
          <p>So the lower the Macaulay duration, the lesser the interest rate risk.</p>
          <br/>
          <div className={styles["separator"]}></div>
          <br/>
          <h5>EVALUATING LIQUIDITY RISK</h5>
          <p>The third risk we have to evaluate, is the liquidity risk. </p>
          <p>This is simply to check if the Debt fund has the money to pay back the customers when they withdraw their money.</p>
          <br/>
          <p>So Debt Fund Managers take the money they get from investors like you and me, and they give it as debt to Companies.</p>
          <br/>
          <p>Unlike Stock markets, where you can sell the stock anytime to get money, it difficult to get the money whenever you want.</p>
          <p><span style={{color: "var(--secondary-highlight)"}}><i><b>One way to get money you want, is to sell the debt.</b></i></span></p>
          <br/>
          <div className={styles["box"]}>
            <img src={debt}/>
            <br/>
            <p>Eg: If Debt Fund A has given Rs. 100 as debt to Company X at 8% interest, and now they want that money back immediately, what they can do is sell this debt to Debt Fund B. So Debt Fund B pays Rs. 100 to Debt Fund A and now Company X has to make payments to Debt Fund B.</p>
          </div>
          <br/>
          <p><i><b>This is where the whole debt becomes more valuable/ less valuable comes into picture.</b></i></p>
          <br/>
          <div className={styles["box"]}>
            <p>So if the debt that Debt Fund A owns becomes more valuable, they would be able to sell it Rs. 108, if it becomes less valuable, they would only be able to sell it at Rs. 92.</p>
            <img src={bag}/>
          </div>
          <br/>
          <p>As you must have understood, selling debt isn't very straightforward.</p>
          <br/>
          <p>So what happens when a lot your investors withdraw their money and the Debt Fund isn't able to sell the debt they own? ü§î</p>
          <p>They end up having to borrow money, so that they can give the money back to the investors. This is usually not a good position to be in üëé</p>
          <br/>
          <div className={styles["box"]}>
            <p style={{color: "var(--secondary-highlight)"}}><i><b>So how to check this?</b></i></p>
            <p>Easy. Just check the Cash Holdings of the debt fund.</p>
            <p>If it is negative, it means that they are borrowing money to repay their investors.</p>
            <p><i>It is usually a good time to exit the fund  üëç</i></p>
          </div>
          </div>
      </div>
    );
  }
}

export default EvaluatingDebtFunds;