import React from 'react';

import styles from './styles.module.scss';
import tick from './tick.svg';
import cross from './cross.svg';

var alpha = ["A", "B", "C", "D"];

export class Quiz extends React.Component{
    constructor(props){
        super(props);
        var questions = this.props.questions.map((question)=>{
            return {...question, selected: null}
        });
        this.state = {
            questions: questions
        }
    }

    select=(i,j)=>()=>{
        console.log(i,j);
        var questions = this.state.questions;
        if(questions[i].selected ==null){
            questions[i].selected=j;
            this.setState({questions: questions});
        }
    }

    render(){
        console.log(this.state.questions);
        var questions = this.state.questions.map((question,i)=>{
            var options = question.options.map((option, j)=>{
                var highlight = 
                    question.selected==null?"":
                    question.answer==j?styles["correct"]:
                    question.selected==j?styles["wrong"]:"";
                return (
                    <div className={styles["option"] + " " + highlight} onClick={this.select(i,j)}>
                        <div className={styles["number"]}><span>{alpha[j]}</span><img src={tick} className={styles["tick"]}/><img src={cross} className={styles["cross"]}/></div>{option}
                        </div>
                )
            })
            return(
                <div className={styles["question-container"]}>
                    <div className={styles["question"]}>{question.question}</div>
                    <div className={styles["options"]}>
                        {options}
                    </div>
                </div>
            )
        });
        return (
            <div className={styles["quiz"]}>
                <div className={styles["header"]}>
                  <h3 className={styles["sub-title"]}>L1 : THE ABSOLUTE BASICS</h3>
                  <div className={styles["title-box"]}>
                    <div className={styles["container"]}>
                      <h3 className={styles["title"]}>Pop Quiz</h3>
                      <div className={styles["title-line"]}></div>
                    </div>
                  </div>
                </div>
                <div className={styles["questions"]}>
                    {questions}
                </div>

            </div>
        )
    }
}