import React from 'react';

import styles from 'styles/post.module.scss';
import graph from './graph.png';
import credit from './credit.svg';
import before from './before.svg';
import PlaystoreBanner from 'components/play-store';

class DebtFunds1 extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L3 : WTF ARE MUTUAL FUNDS</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Debt Funds</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <div className={styles["tldr"]}>
                <h2 className={styles["heading"]}>‚ö°Ô∏è Here‚Äôs what this lesson is all about :</h2>
                <p>In this chapter and the next, we explain how a debt fund works behind the scenes and what the risks are. It's not as boring as it sounds.</p>
            </div>
            <p>Similar to how banks give out loans to people, Mutual Funds give debt to Companies and even the Government!</p>
            <br/>
            <p style={{"color": "var(--secondary-highlight)"}}><i>So when you invest in a debt fund, the previous performance is mostly irrelevant. So all the fancy graphs which you see in any investment app, does not help you make a decision about whether to invest or not.</i></p>
            <p style={{"color": "var(--secondary-highlight)"}}><i>Let us help you figure it out.</i></p>
            <br/>
            <img src={graph}/>
            <br/>
            <p style={{color: "grey", textAlign: "center"}}>Past performance graphs in debt funds are as useful as this pie chart.</p>
            <br/>
            <p style={{fontWeight: 600}}>A Debt fund has primarily, two types of risks : Credit and Interest.</p>
            <br/>
            <h5>CREDIT RISKS</h5>
            <p>Just like how banks give loans to people based on their Credit Score (CIBIL/Experian/CRIF Score), Mutual Funds also give debt based on the Credit Rating, and just like people, the higher the credit rating, the lower the risk.</p>
            <p style={{fontWeight: 600, color: "var(--secondary-highlight)"}}>So the higher the Credit Rating, the lower the risk involved. Lower risk means a lower interest rate.</p>
            <br/>
            <div className={styles["box"]}>
              <img src={credit}/>
            </div>
            <br/><br/>
            <p><i>Mutual Fund X has ‚Çπ200. There's Company Blue with Credit Rating A+ and Company Red with Credit Rating B+.</i></p>
            <p><i>Both companies need ‚Çπ100 as debt. Mutual Fund will give ‚Çπ100 to Company Blue at 5% interest and ‚Çπ100 to Company Red at 9% interest. </i></p>
            <p><i>They charge higher interest to make up for the higher risk in case of Company Red.</i></p>
            <br/>
            <p>So what would happen if Company Red‚Äôs Credit Rating increased after the debt was given? ü§î</p>
            <br/>
            <div className={styles["box"]}>
              <img src={before}/>
            </div>
            <br/>
            <p>Simple, that's really good for you. üëåüèª</p>
            <br/>
            <p>The debt you have give earlier is still being repaid at 9% interest, because you gave it before.</p>
            <br/>
            <p>So that means you are getting a higher interest rate from a company who has a high Credit rating! So the risk has gone down, and your interest rate is still high. Win-Win üí™</p>
            <p>So the debt that you hold becomes <span style={{color: "var(--secondary-highlight)", fontWeight: "600"}}>more valuable</span> (more on this later)</p>
            <br/>
            <p>What would happen if Company Blue‚Äôs Credit Rating goes down after you've given the debt? ü§î</p>
            <br/>
            <p>The opposite of what we described earlier. This means that you are charging low interest rate from a company who has a lower Credit Rating. The risk has gone up, and your interest rate is still Low. üëé</p>
            <p>In this case, your debt is <span style={{color: "var(--secondary-highlight)", fontWeight: "600"}}>less valuable</span>.</p>
            <br/>
            <p><i>Phew, you are halfway done with debt funds.</i></p>
            <p><i>Read on to know about the Interest Risk we talked about earlier.</i></p>
            <br/>
            <PlaystoreBanner text={"Wanna try Savemo Pocket?"}/>
          </div>
        </div>
    );
  }
}

export default DebtFunds1;