import React from 'react';

import styles from 'styles/post.module.scss';
import perfect from './perfect.gif';
import PlaystoreBanner from 'components/play-store';

class DebtFundTypes extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L3 : WTF ARE MUTUAL FUNDS</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Types of Debt Funds</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <p>Cool. Now you know how to evaluate Debt Funds.</p>
            <br/>
            <p>Now let's evaluate a few different types of debt Funds.</p>
            <br/>
            <h5>LIQUID FUND</h5>
                <p><i>Liquid Funds gives debts in which the maximum duration of the loan is 91 days.</i></p>
                <br/>
                <p>So:</p>
                <p><span style={{color: "var(--secondary-highlight)", fontWeight: "900"}}>Credit Risk</span> is low because they give debt to the government and companies with high credit score.</p>
                <br/>
                <p><span style={{color: "var(--secondary-highlight)", fontWeight: "900"}}>Interest Rate</span> is low because the duration is very less.</p>
                <br/>
                <p><span style={{color: "var(--secondary-highlight)", fontWeight: "900"}}>Liquidity Rate</span> is low because, well, they are liquid funds. So they hold a lot of cash.</p>
                <br/>
                <p>So why should you invest in a Liquid Fund?</p>
                <br/>
                <p>Like the name indicates, a liquid fund offers you liquidity, i.e, you can withdraw your money any time you want. Some Mutual Funds even give you instant redemption where the money hits your bank account seconds within you hit withdraw!</p>
                <br/>
                <p className={styles["highlight"]}><b>A Liquid fund also gives you an interest rate of 5.5%-6.5%. This is higher than what most bank accounts offer you.</b></p>
                <br/>
                <p>So investing in a liquid mutual fund gives you the flexibility of a Savings Account and the returns of a Fixed Deposit. This is as close as you can get to having your cake and eating it too.</p>
                <br/>
                <p style={{color: "var(--secondary-highlight)"}}>All of this makes it the best place to save up for Emergency Fund or if you want someplace to stash your money for the short term.</p>
                <br/>
                <img src={perfect}/>
                <br/><br/>
                <div className={styles["separator"]}></div>
                <br/>
                <h5>Ultra-Short/Low/Short<br/>Medium Duration Funds</h5>
                <p>Remember Macaulay‚Äôs Duration?</p>
                <p>(It is used to calculate the time required to recover the money that was invested in the debt)</p>
                <br/>
                <p>Well SEBI has created a way to classify funds based on that. So it's easier for you, as an investor to assess the risk.</p>
                <br/>
                <p>So Based on Macaulay‚Äôs Duration, Debt Funds are classified as:</p>
                <br/>
                <p><span style={{color: "var(--secondary-highlight)", fontWeight: "900"}}>Ultra-Short Term Debt Funds</span></p>
                <p>These are debt funds where the average of Macaulay‚Äôs Duration of all their debts is between <b><i>3 months and 6 months</i></b> (between 0.25 and 0.5).</p>
                <p>Since Macaulay‚Äôs Duration is low here, the interest risk is low.</p>
                <p>However, the Credit Risk Remains. So you have to look at the holdings and figure it out.</p>
                <br/>
                <p><span style={{color: "var(--secondary-highlight)", fontWeight: "900"}}>Low Duration Debt Funds</span></p>
                <p>These are debt funds where the average of Macaulay‚Äôs Duration of all their debts is between <b><i>6 months and 1 year</i></b> (between 0.5 and 1).</p>
                <p>The Macaulay‚Äôs Duration is low here, so the interest rate risk is low.</p>
                <p>These types of funds have Credit Risk which you have to asses by looking at it's holdings</p>
                <br/>
                <p><span style={{color: "var(--secondary-highlight)", fontWeight: "900"}}>Short Duration Debt Funds</span></p>
                <p>These are debt funds where the average of Macaulay‚Äôs Duration of all their debts is between <b><i>1 year and 3 years</i></b> (between 1 and 3).</p>
                <p>Macaulay‚Äôs Duration here is high, so it carries a significant interest rate risk.</p>
                <p>These types of funds have Credit Risk which you have to assess by looking at its holdings</p>
                <br/>
                <p><span style={{color: "var(--secondary-highlight)", fontWeight: "900"}}>Medium Duration Debt Funds</span></p>
                <p>These are debt funds where the average of Macaulay‚Äôs Duration of all their debts is between <b><i>4 years and 7 years</i></b> (between 4 and 7).</p>
                <p>Macaulay‚Äôs Duration here is very high, so this has a very high-interest rate risk.</p>
                <p>These types of funds have Credit Risk which you have to assess by looking at it's holdings</p>
                <br/>
                <div className={styles["separator"]}></div>
                <br/>
                <h5>CREDIT RISK FUNDS</h5>
                <p>Like the name indicates, Credit Risk Funds invest their money in highly risky debt. That is, they give a lot of money to people with lower credit scores in order to get a higher return.</p>
                <br/>
                <p><span style={{color: "var(--secondary-highlight)"}}>This is highly risky and we recommend you <b>don't</b> invest your money in Credit Risk Funds.</span></p>
                <br/>
                <div className={styles["separator"]}></div>
                <br/>
                <h5>GILT FUNDS</h5>
                <p>GILT Funds are a very special lot.</p>
                <p style={{color: "var(--secondary-highlight)"}}>In a GILT fund, at least 80% of the money is used to give debt to the government.</p>
                <br/>
                <p>So the credit risk is virtually zero, because the government always pays back its debt.</p>
                <br/>
                <p>But there is interest rate risk. Make sure to check Macaulay‚Äôs Duration to see the interest rate risk involved.</p>
                <br/>
                <PlaystoreBanner text={"Wanna try Savemo Pocket?"}/>
          </div>
      </div>
    );
  }
}

export default DebtFundTypes;