import React from 'react';

import styles from 'styles/post.module.scss';
import lightbulb from 'styles/lightbulb.svg';
import piggy from './piggy.svg';
import upside from './upside.svg';
import downside from './downside.svg';
import avengers from './avengers.png';

class EvaluatingEquityFunds extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L3 : WTF ARE MUTUAL FUNDS</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Evaluating Equity Funds</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
                <p>Now that you know the different types of Equity funds, let us help you how to choose between them.</p>
                <br/>
                <p>Before we can get started, there‚Äôs one word you really need to understand :</p>
                <br/>
                <h5>BENCHMARK</h5>
                <p>Every fund is benchmarked against an Index (a set of stocks) which closely resembles the stocks it invests in. This helps us compare the performance of the fund.</p>
                <br/>
                <p>Eg: Almost all large cap funds benchmark themselves against the Nifty50 index.</p>
                <br/>
                <div className={styles["box"]}>
                    <img src={lightbulb} style={{width: "40px", display: "inline-block", verticalAlign: "top", margin: "0px 5px"}}/>
                    <p style={{width: "calc(100% - 50px)", verticalAlign: "top", display: "inline-block", color: "var(--secondary-highlight)"}}><b>So to compare the performance of an Equity Mutual Fund, we just have to see how well it performs against the benchmark.</b></p>
                </div>
                <br/>
                <div className={styles["separator"]}></div>
                <br/>
                <p>To evaluate Equity Mutual Funds, you only need to understand 3 values :</p>
                <br/>
                <h5>SHARPE RATIO</h5>
                <b><i><p>Sharpe Ratio gives us the risk adjusted return for a mutual fund.</p>
                <p>It gives us the return for every unit of risk taken.</p></i></b>
                <br/>
                <p>What does that mean?ü§î</p>
                <p>Simply put, it checks if the returns you are getting is worth the risk you are taking.</p>
                <br/>
                <div className={styles["box"]}>
                    <h5>üí° EXAMPLE üí°</h5>
                    <p>Say there are two funds A and B. They both have given 11% returns over the last 3 years.</p>
                    <p>But Sharpe Ratio of Fund A is 0.3 and Sharpe Ratio of Fund B is 0.7</p>
                    <br/>
                    <p>This means that even though both funds gave you the same returns, fund B managed to do so while taking lower risk.</p>
                </div>
                <br/>
                <div className={styles["separator"]}></div>
                <br/>
                <h5>ALPHA</h5>
                <p>Alpha gives us the risk-adjusted returns of the fund compared to the <span style={{color: "var(--secondary-highlight)"}}><b>benchmark</b></span>.</p>
                <br/>
                <p>So here it takes the returns, adjusts it according to the risk taken and compares it with the benchmark.</p>
                <br/>
                <p><i><b>This is useful in comparing funds of the same type.</b></i></p>
                <br/>
                <div className={styles["box"]}>
                    <h5>üí° EXAMPLE üí°</h5>
                    <img src={piggy}/>
                    <br/><br/>
                    <p>Say there are two Large Cap funds A and B. They both have given 11% returns over the last 3 years.</p>
                    <p>Fund A‚Äôs alpha is 1% and alpha of Fund B is 3%.</p>
                </div>
                <br/>
                <p><span style={{color: "var(--secondary-highlight)"}}><b><i>So both funds performed the same, right?  ü§î</i></b></span></p>
                <p>Nope. Fund B gave 3% higher returns than the benchmark and Fund A gave 1% higher returns than the Benchmark when you factor in the risk taken.</p>
                <br/>
                <p className={styles["highlight"]}>An important point to note here is that Alpha is measured to the benchmark. So you might see alpha in the negative.<br/>It does not always mean the fund performed poorly. </p>
                <br/>
                <p>It means it took a higher risk to get the returns than the benchmark. So use this as a comparison tool between two funds, don't use this in isolation.</p>
                <br/>
                <div className={styles["separator"]}></div>
                <br/>
                <h5>CAPTURE RATIOS</h5>
                <p>Capture Ratios sound complicated, but don‚Äôt worry they are really simple and are very useful üëÄ</p>
                <br/>
                <p>As you know, every fund has a benchmark. Over a period of time, the value of the benchmark will keep moving upüìà and downüìâ (because the stock market always fluctuates).</p>
                <br/>
                <p>The <span style={{color: "var(--secondary-highlight)"}}><b><i>Upside Capture Ratio</i></b></span> tells you how much the fund moved upüìà when the benchmark moved up.</p>
                <br/>
                <p>The <span style={{color: "var(--secondary-highlight)"}}><b><i>Downside Capture Ratio</i></b></span> tells you how much the value of the fund went downüìâ when the benchmark moved down.</p>
                <br/>
                <div className={styles["box"]}>
                    <h5>üí° EXAMPLE üí°</h5>
                    <img src={upside}/>
                    <p>üëÜüëÜüëÜ Here if you notice, the upside capture ratio of the Fund A is high, but the downside is high too</p>
                    <br/>
                    <img src={downside}/>
                    <p>üëÜüëÜüëÜ For Fund B, the upside capture ratio isn't as high as Fund A, but the Downside Capture Ratio is very low.</p>
                </div>
                <br/>
                <p>This helps you make a choice according to the risk you want to take.</p>
                <p style={{fontWeight: "600"}}><i>If you want high returns and are comfortable with high risk, you'd want to choose Fund A.</i></p>
                <br/>
                <p style={{fontWeight: "600"}}><i>If you are happy with decent returns with lower risk, Fund B would be the right choice for you.</i></p>
                <br/>
                <div className={styles["separator"]}></div>
                <br/>
                <div className={styles["box"]}>
                    <p style={{textAlign: "center"}}>‚ö†Ô∏è One small thing ‚ö†Ô∏è</p>
                    <p style={{textAlign: "center", color: "var(--secondary-highlight)", fontWeight: "600"}}>It might be pointless to use these values to compare different types of fund like a Large Cap Fund and Small Cap Fund.</p>
                </div>
                <br/>
                <p>Boom! You know all you need to know to choose between different Equity Mutual Funds.</p>
                <p>Let‚Äôs take your new-found knowledge for a spin.</p>
                <br/>
                <img src={avengers}/>
          </div>
      </div>
    );
  }
}

export default EvaluatingEquityFunds;