import React from 'react';

import styles from 'styles/post.module.scss';
import thinking from './thinking.gif';
import graph from './graph.png';

class WhatHappensToMyMoney extends React.Component {

    render(){
        return (
            <div className={styles["post"]}>
                <div className={styles["header"]}>
                    <h3 className={styles["sub-title"]}>L3 : WTF ARE MUTUAL FUNDS</h3>
                    <div className={styles["title-box"]}>
                        <div className={styles["container"]}>
                            <h3 className={styles["title"]}>What happens to<br/>my money?</h3>
                            <div className={styles["title-line"]}></div>
                        </div>
                    </div>
                </div>
                <div className={styles["post-content"]}>
                    <div className={styles["tldr"]}>
                        <h2 className={styles["heading"]}>‚ö°Ô∏è Here‚Äôs what this lesson is all about :</h2>
                        <p>Mutual Funds take money from a lot of people (like you and me) and invest it on our behalf.</p>
                        <br/>
                        <p>Curious to know how that works? Read on üëá</p>
                    </div>
                    <p>Mutual Funds are for people who want decent returns and don't want to spend a lot of time thinking about where to invest.</p>
                    <br/>
                    <p>There are around 40 Asset Management Companies (AMCs) in India that run these Mutual Funds. ICICI, HDFC and Tata are all examples of AMCs.</p>
                    <br/>
                    <p>Each of these AMCs also have around 40-50 Mutual Funds. That's close to 2000 Mutual Funds to choose from üòÆ !! As a result, most Mutual Fund apps look like Amazon.</p>
                    <br/>
                    <img src={thinking}/>
                    <br/><br/>
                    <p style={{color: "var(--secondary-highlight)"}}>The fundamental thing you have to understand when it comes to investing money is that the more risk you take, the higher the returns you can possibly earn. </p>
                    <br/>
                    <p>The only way to overcome risk is to stay invested for long periods of time. </p>
                    <p>So it comes down to this, if you want to get higher returns, you'd have to make riskier investments and you have to stay invested for longer periods of time.</p>
                    <br/>
                    <img src={graph}/>
                    <p style={{color: "grey", textAlign: "center"}}>This is an important graph</p>
                    <br/>
                    <p className={styles["highlight"]}>Hence, if you can <i><b>only</b></i> invest for a shorter period of time, then you should take lower risk, and obviously, the returns would be lower.</p>
                    <br/>
                    <p>There are different funds that cater to a variety of needs. There's one to help you save for your Bike in a couple of years and there's another that helps you save for your retirement.</p>
                    <br/>
                    <div className={styles["separator"]}/>
                    <br/>
                    <p>We all know that Mutual Funds invest in Stocks. The thing with investing in stocks is that it is quite risky. So you need to be invested for a long term to get returns.</p>
                    <br/>
                    <div className={styles["box"]}>
                        <p>In order to give you some place where you can stay invested for the short term (less than 3 years), the AMCs have debt funds.</p>
                        <br/>
                        <p style={{color: "var(--secondary-highlight)", fontWeight: "600"}}><i>Debt Funds take your money and instead of investing in stocks, they give this money as loans to companies and even the Government.</i></p>
                    </div>
                    <br/>
                    <p>The great thing about loans is that the duration is fixed.</p>
                    <br/>
                    <p>So if you want a place to invest your money for 1 year, all you have to do is choose a debt fund where the average loan duration is 1 year.</p>
                    <br/>
                    <p>Now before you get started learning more about different types of mutual funds, you have to know how much these Mutual Funds charge and how the value of these Mutual funds are decided.</p>
                    <br/>
                    <p>Read on üëáüëáüëá</p>
                </div>
            </div>
        );
    }
}

export default WhatHappensToMyMoney;