import React from 'react';

import styles from 'styles/post.module.scss';
import lend from './lend.svg';
import lower from './lower.svg';
import upper from './upper.svg';
import tooMuch from './too-much.gif';
import PlaystoreBanner from 'components/play-store';

class DebtFunds2 extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L3 : WTF ARE MUTUAL FUNDS</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Debt Funds: Contd.</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <h5>INTEREST RISKS</h5>
            <p>The base interest rates in country are set by the Reserve Bank of India. If the base rate reduces, the interest rate of all debts issued after the change, reduces.</p>
            <p>If the base rate increases, the interest rate of all debt issues after the change increases.</p>
            <br/>
            <p>So here the risk is, what happens if the interest rate changes after a debt fund has given debt to a company? ü§î</p>
            <p>Confused? We'll explain:</p>
            <br/>
            <div className={styles["box"]}>
              <p><i>Say, Mutual Fund A has given debt of ‚Çπ100 to Company Blue at 6% interest.</i></p>
              <br/>
              <img src={lend}/>
            </div>
            <br/><br/>
            <p>Now, if RBI lowers the base interest rate from 6% to 5%, then all debt given afterwards will have a lower interest rate.</p>
            <p>So if Mutual Fund B gives ‚Çπ100 as debt to Company Blue, it'll be at <i><b>lower interest rate</b></i>.</p>
            <br/>
            <div className={styles["box"]}>
              <img src={lower}/>
              <br/>
              <p style={{color: "grey", textAlign: "center", fontSize: "12px"}}>AFTER LOWERING INTEREST RATE</p>
            </div>
            <br/>
            <br/>
            <p>That means that Debt Fund A, has a debt which is getting repaid at 6%, meanwhile the rest of the country can only give debt to Company Blue at 5%.</p>
            <p><span style={{color: "var(--secondary-highlight)"}}><i><b>This means the debt that Debt Fund A owns is more valuable.</b></i></span></p>
            <br/><br/><br/>
            <p>So what if RBI increases the interest rate? ü§î</p>
            <br/>
            <div className={styles["box"]}>
              <img src={upper}/>
              <br/>
              <p style={{color: "grey", textAlign: "center", fontSize: "12px"}}>AFTER INCREASING INTEREST RATE</p>
            </div>
            <br/>
            <p>The opposite happens.</p>
            <p>Everyone will lend to Company Blue at a higher interest rate of 7%, meanwhile Debt Fund A is stuck with 6%.</p>
            <p><span style={{color: "var(--secondary-highlight)"}}><i><b>This means the debt that Debt Fund A owns is less Valuable.</b></i></span></p>
            <br/>
            <div className={styles["box"]}>
              <p>So Interest Rate risk is the risk that RBI changes the interest rate(this can happen for a variety of reasons).</p>
              <p><i><b>Interest Rate risk goes up with the duration of the debt.</b></i></p>
            </div>
            <br/>
            <p><i>And now, you know everything about debt funds. But, how do you select which ones to invest in?</i></p>
            <p><i>Read on to find out how to choose a debt fund that doesn‚Äôt suck :</i></p>
            <br/>
            <img src={tooMuch}/>
            <br/>
            <PlaystoreBanner text={"Wanna try Savemo Pocket?"}/>
          </div>
      </div>
    );
  }
}

export default DebtFunds2;